var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"light-bg pt-5",attrs:{"id":"about"}},[_c('div',{staticClass:"container mt-1 p-5"},[_c('div',[_c('h2',{staticClass:"oswald display-1 text-end pb-3"},[_vm._v("Claudio Rodríguez")])]),_c('div',{staticClass:"d-flex"},[_c('div',{staticClass:"px-5 picture-container h-auto mb-4"},[_c('div',{staticClass:"shadow-lg",attrs:{"id":"aboutPicture"}})]),_c('div',[_c('p',[_vm._v(" Nace en Mérida, Venezuela, en febrero del año 1967. Estudia en la Escuela de Artes Plásticas Cristóbal Rojas de Caracas, donde se inicia en 1984. En el año 1985 asiste al Taller de Historia de la Pintura dictado por Fernando de Szyslo en el Museo de Bellas Artes de Caracas y realiza pasantías en el Taller de Serigrafía de Simón Guerrero, Caracas. Dos años después continúa su formación en el Instiuto de Arte Federico Brandt, también en Caracas, donde al año de su ingreso se hace acreedor de una beca de estudios por su rendimiento y capacidad creadora. ")]),_c('p',[_vm._v(" Seguidamente continua sus estudios como becario en el Instituto Superior de Arte de La Habana. Entre las exposiciones individuales de Claudio Rodríguez se cuentan Fantasías y obsesiones (1986) en la Galería Viva México; Pinturas, en la Galería Sotavento en el año 1988; Piedra, Papel y Tijera (1990) en la Sala Mendoza. En 1991 regresa a la Galería Sotavento con Mis Últimos Personajes. Y en el 2001 expone 161 obras en el Museo Alejandro Otero: “Yo mismo Yo. De la melancolía y el narcisismo en el arte.” (Todos estos espacios expositivos ubicados en Caracas.) ")])])]),_c('p',[_vm._v(" Algunas de las muestras colectivas en las que ha tomado parte son: el 43 Salón Arturo Michelena de Valencia y el VI Salón de Occidente realizado en Mérida, ambos en 1985. En 1986 participa en el VI Salón Municipal de Pintura de Maracay; la I Bienal de Artes Visuales de Oriente que tuvo lugar en Cumaná y, posteriormente, en una nueva edición del Salón Arturo Michelena (2000). Participa en Nuevas Adquisiciones, abierta en la Galería de Arte Nacional de Caracas en 1987. Dos años más tarde su obra es incluida en Un siglo de flores presentada en la Galería Los Espacios Cálidos del Ateneo de Caracas. Además, también se cuenta para este mismo año Amazonia III, muestra que itineró entre el Museo de Arte Contemporáneo Francisco Narváez de Porlamar, el Centro de Bellas Artes de Maracaibo y Sala Mendoza de Caracas. En 1988 participa en el Salón Nacional de Artes Plásticas y Nuevas Adquisiciones de la Galería de Arte Nacional”. También en el Festival de Artistas Venezolanos en el Espacio de Arte PH de Corp Banca. En 1989 participa en la colectiva ZOO de la Galería Clave de Arte. El Palacio de Gobierno del Distrito Federal (Caracas) lo recibe en el año 1990, en la exposición “Arte Emergente – Nuevas Propuestas de los 90.” En el mismo año lo recibe la Galería Astrid Paredes de Caracas. Asimismo, se presenta en el Ateneo de Caracas con El autorretrato en la pintura venezolana (1820-1989). Un año después viaja a La Habana con la exhibición Cinco Venezolanos (organizada por La Casa de Las Américas). ")]),_c('p',[_vm._v(" En 1992 es invitado a integrar la colectiva internacional Entre Trópicos Artistas contemporáneos del Grupo de Río en el Museo de Arte Contemporáneo Sofía Imber. Al año siguiente se inaugura, con la asistencia de Claudio Rodríguez, el Salón Pirelli de Jóvenes Artistas. En 1995, participa en la colectiva Colección Oberto. Exposiciones realizadas todas en los espacios del MACSI. Este mismo año participa en el I Salón Nacional de Artes Visuales del Museo Alejandro Otero, Caracas. Y en el I Salón Nacional de Dibujo del mismo museo. Y asimismo se presenta en Bogotá, Colombia: Imagen de Don Andrés Bello. Secretaría Ejecutiva del Convenio Andrés Bello. ")]),_c('div',{staticClass:"d-flex me-4"},[_c('div',[_c('p',[_vm._v(" El Mirar de la Mirada, 1996, y Re-Figuraciones, 1998, tienen lugar en la Galería de Arte Nacional, donde se exhibe, entre otras, la creación artística de Rodríguez. En 1999 la Galérie Passage du Retz de París es su escenario, con la exhibición Identités: Artistes d’Amerique Latine et des Caraïbes. (Exhibición auspiciada por el Banco Interamericano de Desarrollo, el Centro George Pompidou y la Escuela Nacional de Bellas Artes de Francia. Durante el 2000 se presenta en una nueva edición del Salón Arturo Michelena y en la Galería Lía Blasini de Caracas. Las distinciones recibidas por este creador venezolano incluyen el Tercer Premio de Fundasol, Un día pintando en la ciudad de Mérida, otorgado en Mérida en 1982. En 1986 le es otorgado el Premio Harry Liepins, como estímulo a las nuevas manifestaciones de las artes visuales venezolanas. Para 1990 recibe el Premio Universidad de Los Andes, con motivo de la I Bienal Nacional de Artes Plásticas de Mérida. El Premio Fama es recibido por Rodríguez en 1994 otorgado por la Fundación Polar y la Fundación Ayacucho. ")])]),_c('div',{staticClass:"text-center d-flex align-items-center pb-4 ms-4",staticStyle:{"width":"200%"}},[_c('div',{attrs:{"id":"secondAbout"}})])]),_c('p',[_vm._v(" En sus primeros años de actividad artística Claudio Rodríguez estuvo muy ligado al maestro Oswaldo Vigas, compartiendo asiduamente. Consejo tras consejo el maestro Vigas lo ayudó enormemente. Su aprecio y cariño por Claudio Rodríguez fue infinito, al punto que ambos, serrucho, martillo y clavos en mano, enmarcaron con listoncitos todos los lienzos de la primera exposición individual de Claudio en Caracas. Entre abril y julio del año 2001 expone 161 piezas, bajo la curaduría de Ruth Auerbach, en la Sala Seis del Museo Alejandro Otero de La Rinconada. Luego de otras exposiciones y actividades, Claudio Rodríguez se retira a Mérida, donde un tanto alejado del ruido de la ciudad como de la gente, comienza un proceso de liberación y búsqueda de sí mismo, a través de la introspección, la lectura, el análisis de obras importantes y otras actividades de crecimiento interior. A principios del año 2017 Claudio Rodríguez decide volver a ejecutar enérgicamente su arte. Su fuerza interior desea comenzar un nuevo periodo. Así, se lanza a trabajar frenéticamente. Primero, con lápiz sobre papel, luego lapicero sobre papel, hasta que retoma el óleo, con quien continúa de la mano. ")])])])
